<template>
    <article class="page-merge">
        <div class="page-merge__left">
            <div class="page-merge__left__content">
                <canvas ref="canvas" :width="width" :height="height" />
                <ul>
                    <li v-for="backgroundURL in backgroundURLs" :key="backgroundURL">
                        <img :src="backgroundURL" @click="changeBackground(backgroundURL)" />
                    </li>
                </ul>
                <button class="a-button a-button--success a-button-save" @click="save">
                    <i class="ion-md-image" /> 保存
                </button>
            </div>
        </div>

        <div class="page-merge__middle">
            <ul>
                <li class="img" v-for="{src} in imagesUploaded" :key="src">
                    <img :src="src" />
                </li>
                <li>
                    <ButtonLoadFile
                        @loaded="onImageLoaded"
                        class="button-add p-0 a-button a-button--dark a-button--outline"
                    >
                        <i class="ion-md-folder-open" /> 打开
                    </ButtonLoadFile>
                </li>
            </ul>
        </div>

        <div class="page-merge__right"></div>
    </article>
</template>
<script>
import { saveAs } from 'file-saver';
import loadImage from '../../../packages/load-image/dist/index.es';
import ButtonLoadFile from './ButtonLoadFile';
import { POS } from './Merge.config';
export default {
    name: 'Merge',

    components: { ButtonLoadFile },

    data() {
        return {
            width: 1300,
            height: 5500,
            context: null,
            backgroundImage: null,
            imagesUploaded: [],
            backgroundURLs: [
                'sock_bg/AFS32.png',
                'https://cdn.shopifycdn.net/s/files/1/0276/2922/4000/files/merchant-taking-sale-with-chip-and-swipe-reader.jpg?v=1593507608',
            ],
        };
    },

    async mounted() {
        this.context = this.$refs.canvas.getContext('2d');
        this.backgroundImage = await loadImage(this.backgroundURLs[0]);
        this.context.drawImage(this.backgroundImage, 0, 0);
    },

    methods: {
        save() {
            this.$refs.canvas.toBlob(function (blob) {
                saveAs(blob, 'merge.png');
            },'image/png',1);
        },

        async changeBackground(url) {
            this.backgroundImage = await loadImage(url);
            this.render();
        },

        onImageLoaded(data) {
            for (const { source } of data) {
                const { img } = source;
                this.imagesUploaded.push(img);
            }
            this.render();
        },

        render(data) {
            const { context } = this;
            context.clearRect(0, 0, this.width, this.height);
            context.drawImage(this.backgroundImage, 0, 0, this.width, this.height);
            if (0 < this.imagesUploaded.length) {
                const imageLength = this.imagesUploaded.length;
                for (const [index, { x, y, w, h }] of POS.entries()) {
                    const img = this.imagesUploaded[index % imageLength];
                    context.drawImage(img, 0, 0, img.width, img.height, x - w / 2, y - h / 2, w, h);
                }
            }
        },
    },
};
</script>

<style scope lang="scss">
.page-merge {
    display: flex;
    box-sizing: border-box;
    height: 100vh;
    &__left {
        position: relative;
        overflow: hidden;
        height: 100%;

        padding: 16px;

        &__content {
            display: flex;
            position: relative;
            height: 100%;
            width: 100%;
            box-shadow: 0 0 5px 1px rgba(#000, 0.26);
            canvas {
                height: 100%;
            }

            ul {
                border: 1px solid #ddd;
                width: 80px;
                overflow-x: hidden;
                overflow-y: scroll;
                li {
                    width: 100%;
                    height: 100px;
                    & + li {
                        border-top: 1px solid #ddd;
                    }
                    img {
                        display: block;
                        width: 100%;
                        height: 100px;
                        object-fit: contain;
                    }
                }
            }

            > .a-button-save {
                position: absolute;
                left: 16px;
                bottom: 16px;
                width: 120px;
            }
        }
    }

    &__middle {
        flex: 1;
        padding: 16px;
        ul {
            display: flex;
            li.img {
                list-style: none;
                height: 120px;
                width: 120px;
                border: 1px solid #ddd;
                & + li {
                    margin-left: 8px;
                }

                &.active {
                    box-shadow: 0 0 6px 6px rgba(#000, 0.26);
                }

                img {
                    width: 120px;
                    height: 120px;
                    display: block;
                    object-fit: contain;
                }
            }

            li {
                .button-add {
                    height: 120px;
                    width: 120px;
                    line-height: 120px;
                    font-size: 24px !important;
                }
            }
        }
    }
}
</style>